@import './reset';
@import './variables';
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;1,400&family=Poppins:ital,wght@0,600;1,600&display=swap');

body {
    background-color: #eaeaea;
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
}

@media all and (max-width: 900px) {
    body {
        font-size: 80%;
    }
}

html {
    scroll-behavior: smooth;
}

h1,h2,h3,h4,h5,h6,button,.sub-nav a, .sub-nav span {
    font-family: 'Poppins', sans-serif;
}

.side-by-side {
    display: flex;
    align-items: flex-start;

    @media all and (max-width: 900px) {
        display: block;
    }
}

.space-between {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header {
    max-width: 1280px;
    margin: auto auto 45px auto;

    .logo {
        width: 180px;
        padding: 15px;
    }

    .sub-nav {
        display: block;

        a, span {
            display: inline-block;
            padding: 25px 25px;
            text-decoration: none;
            color: #111;
            cursor: pointer;
        }

        a:hover, span:hover {
            background-color: #DEDEDE;
        }

        @media all and (max-width: 900px) {
            display: none;
            position: absolute;
            top: 60px;
            right: 15px;
            width: 75%;
            box-shadow: -6px 6px 32px -10px #656565;
            background-color: #FFF;
            border-radius: 12px;
            overflow: hidden;

            &.open {
                display: block;
            }

            a, span {
                display: block;
                padding: 18px 15px;
            }
        }
    }

    .navbar-button {
        text-align: center;
        border: none;
        padding: 12px;
        background-color: transparent;
        cursor: pointer;
        display: none;

        img {
            width: 30px;
        }
    }

    @media all and (max-width: 900px) {
        margin-bottom: 25px;

        .logo {
            width: 90px;
            padding: 10px;
        }

        .navbar-button {
            display: block;
        }
    }
}

.hero {
    background-color: #FFF;
    padding-bottom: 65px;
    position: relative;
    z-index: 1;

    .svg-1 {
        position: absolute;
        z-index: -1;
        bottom: -50px;
        left: -50px;
        width: 150px;
    }

    .svg-2 {
        position: absolute;
        z-index: -1;
        bottom: 50px;
        right: -50px;
        width: 150px;
    }

    .inner-content {
        align-items: center;
        max-width: 900px;
        margin: auto;

        h1 {
            font-size: 2.75rem;
            font-weight: bolder;
            margin-bottom: 15px;
        }

        h2 {
            margin-bottom: 25px;

            span {
                display: inline-block;
                margin-right: 15px;
            }

            .school {
                font-weight: bolder;
                border-left: solid 2px #111;
                padding-left: 15px;
            }
        }

        .cta-container {
            margin-top: 25px;

            a {
                text-decoration: none;
                color: #111;
                padding: 12px 32px;
                border-radius: 500px;
                background: #FFF;
                box-shadow: -2px 2px 15px -5px #6e6e6e;
                margin-right: 15px;
                display: inline-block;
                overflow: hidden;
                position: relative;
                z-index: 1;

                &:before {
                    content: "";
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    width: 100%;
                    height: 100px;
                    background-color: $color-orange;
                    transform: translateY(100px);
                    transition: transform 0.24s cubic-bezier(0.19, 1, 0.22, 1);
                }

                &:hover {
                    color: #FFF;

                    svg {
                        stroke: #FFF;
                    }

                    &:before {
                        transform: translateY(0px);
                    }
                }

                .inner {
                    display: flex;
                    align-items: center;
                }

                svg {
                    margin-right: 15px;
                }
            }
        }
    }

    .about-me {
        .desc {
            line-height: 1.85rem;
        }
    }

    .profile-image {
        max-width: 270px;
        margin-right: 35px;
    }

    @media all and (max-width: 900px) {
        .profile-image {
            display: table;
            margin: auto;
            width: 75%;
        }

        .svg-2 {
            right: 25px;
            bottom: 10px;
        }

        .svg-1 {
            bottom: -15px;
            width: 20%;
        }

        .about-me {
            .details {
                padding: 18px;

                .desc {
                    font-size: 1rem;
                }
            }
        }
    }

    @media all and (max-width: 720px) {
        .inner-content {
            h1 {
                font-size: 1.8rem;
            }

            h2 {
                margin-bottom: 15px;
                font-size: smaller;

                span {
                    display: inline-block;
                    margin-right: 5px;
                }
    
                .school {
                    font-weight: bolder;
                    border-left: solid 2px #111;
                    padding-left: 5px;
                }
            }

            .cta-container {
                a {
                    display: block;
                    margin-bottom: 15px;
                    padding: 18px 24px;
                }
            }
        }
    }
}

.experience {
    max-width: 1280px;
    margin: auto;
    padding: 105px 25px 65px;

    .side-bar {
        min-width: 290px;
        margin-right: 30px;
        align-self: flex-start;
        position: sticky;
        top: 20px;
        position: -webkit-sticky; /* for Safari */

        li {
            display: block;
            padding: 16px 16px;
            border-radius: 6px;
            margin-bottom: 15px;
            cursor: pointer;

            &:hover {
                background-color: #c5c5c5;
            }

            &.selected {
                background-color: #FFF;
                box-shadow: 0px 5px 15px -5px #AAA;
                
                &:hover {
                    background-color: #FFF;
                }
            }
        }
    }

    .main-header {
        font-size: 2.5rem;
        margin-bottom: 25px;
    }

    .markdown-item {
        min-height: 400px;
    }

    .directions-btn {
        align-items: center;
        justify-content: center;

        button {
            color: #3d2514;
            padding: 12px 32px;
            border-radius: 12px;
            border: solid 3px;
            margin-right: 15px;
            font-size: 0.95rem;
            align-items: center;

            &:hover {
                background-color: #3d2514;
                color: #eaeaea;
                cursor: pointer;
            }
        }
    }

    @media all and (max-width: 900px) {
        padding: 65px 20px;

        .main-header {
            margin-bottom: auto;
        }
        
        .side-bar {
            position: relative;
            margin: auto;
            min-width: unset;
            display: block;

            ul {
                display: block;
                margin: auto;
            }
        }

        .markdown-item {
            margin-top: 65px;
        }

        .directions-btn {
            display: flex;
            align-self: flex-start;
            position: sticky;
            bottom: 25px;
            position: -webkit-sticky; /* for Safari */

            button {
                display: flex;
                padding: 8px 24px;
            }
        }
    }
}

.contact {
    background-color: #FFF;
    padding: 60px 25px 0px;
    position: relative;
    z-index: 1;

    .decor {
        position: absolute;
        top: 35px;
        right: 0;
        left: -150px;
        height: 80%;
        z-index: -1;
        opacity: 0.65;
    }
    
    a, & {
        color: #3d2514;
        text-decoration: none;
    }

    .inner {
        max-width: 640px;
        margin: auto auto 40px;

        h1 {
            font-size: 2.5rem;
            margin-bottom: 40px;
        }

        ul {
            background-color: #FFF;
            box-shadow: -5px 5px 32px -10px #684d3a;
            display: table;
            border-radius: 12px;
        }

        li {
            padding: 25px;
            align-items: center;

            line-height: 1.5rem;

            &:hover {
                background-color: #DEDEDE;
            }

            svg {
                margin-right: 20px;
            }
        }
    }

    .copyright {
        font-size: smaller;
        text-align: center;
        padding: 20px;
        border-top: solid thin #b19887;
        margin-top: 20px;
    }

    @media all and (max-width: 900px) {
        .side-by-side {
            display: flex;
        }

        ul {
            width: 45%;
        }
    }

    @media all and (max-width: 720px) {
        .inner {
            display: block;
            max-width: 85%;
            margin: auto;

            h1 {
                font-size: 1.5rem;
            }
        }

        ul {
            display: block;
            width: 100%;
            margin: auto;
        }
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.markdown-item {
    h1,h2,h3,h4,h5,h6 {
        line-height: 1.85rem;
    }

    h1 {
        font-size: 2.5rem;
        margin-bottom: 25px;
    }

    h2 {
        font-size: 1.75rem;
        margin-bottom: 25px;
        margin-top: 12px;
    }

    h2, h4 {
        &:before {
            content: "Role";
            color: $color-orange;
            margin-right: 15px;
        }
    }

    h2:nth-of-type(1) {
        & + p > img {
            width: 100%;
        }
    }

    h3 {
        font-size: 1.5rem;
        margin-top: 8px;
        margin-bottom: 10px;
    }
    
    h4 {
        font-size: 1.25rem;
        margin-top: 6px;
        margin-bottom: 8px;
    }

    b {
        font-weight: bolder;
    }

    a {
        color: orangered;
        text-decoration: underline;
    }

    p, li {
        line-height: 1.85rem;
    }

    p {
        padding-bottom: 20px;
    }

    img {
        display: inline-block;
        width: 45%;
        margin: 15px;
    }

    ul {
        list-style-type: disc;
        padding-bottom: 25px;
    }

    li {
        padding: 5px;
        margin-left: 15px;
    }

    img:nth-child(3n + 3) {
        max-width: 640px;
        display: block;
    }

    @media all and (max-width: 900px) {
        h1,h2,h3,h4,h5,h6 {
            line-height: 2.50rem;
        }

        h1 { font-size: 2.0rem }

        h2 {
            font-size: 1.35rem;
            margin-bottom: 15px;
            margin-top: 10px;
            line-height: 1.75rem;
        }
    
        h2, h4 {
            &:before {
                margin-right: 10px;
            }
        }

        img {
            display: block;
            width: 100%;
            margin: 15px auto;
        }
    
        h2:nth-of-type(1) {
            & + p > img {
                width: 100%;
                max-width: unset;
                margin: 15px auto 5px;
            }
        }
    
        h3 {
            font-size: 1.25rem;
            margin-top: 6px;
            margin-bottom: 8px;
        }
        
        h4 {
            font-size: 1.00rem;
            margin-top: 4px;
            margin-bottom: 6px;
        }
    }
}